<template lang="">
     <div class="container">
        <div class="row">
            <div class="col-md-6" v-for="recipe in recipes" :key="recipe">
                <RecipeCard :imageURL="recipe.imageURL" :ad="recipe.name" :recipeId="recipe.id">
                    
                </RecipeCard> 
            </div>
         

        </div>
    </div> 
    
<!-- loading -->
    <div v-if="loading">
        <div class="conrainer">
            <div class="row">
                <div class="col">
                    <SpinnerComponent></SpinnerComponent>
                </div>
            </div>
        </div>
    </div>


<!-- error -->
    <div v-if="!loading && errorMessage" >
        <div class="container">
            <div class="row">
                <div class="col">
                    <p class="h3 text-danger fw-bold">{{errorMessage}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import SpinnerComponent from '@/components/SpinnerComponent.vue';

  import RecipeCard from '@/components/RecipeCard.vue';
import { RecipeService} from "@/services/RecipeService"
export default {
    data: function () {
        return {

            loading: false,
            recipes: [], 
            errorMessage: null
        };
    },
    created: async function () {
        try {
            this.loading = true;
            let response = await RecipeService.getAllRecipes();
            this.recipes = response.data;
            this.loading = false;
        }
        catch (error) {
            this.errorMessage = error;
            this.loading = false;
        }
    },
    components: { RecipeCard ,SpinnerComponent}
}
</script>
