<template >
       <div class="container my-5">

        
        <div class="row">
            <div class="col text-center ">
                <p class="h1 fw-bold">{{recipe.name}}</p>

            </div>
        </div>




        <div class="row">
            <div class="card p-0">
                <div class="card-header">
                    <h1 class="text-center p-2 text-success">İngredients</h1>
                </div>
                
                <div class="card-body">

                    <ul class="list-group list-group-flush" >

                        <li class="list-group-item" v-for="ing in recipe.ingredients" :key="ing"><span class="fw-bold">{{ing.name}} </span> : {{ing.quantity}}</li>
                    
                    </ul>
                </div>
            </div>
                
        </div>


        <div class="row my-4">
            <div class="card p-0">
                <div class="card-header">
                    <h1 class="text-center p-2 text-success">Steps</h1>
                </div>

                <div class="card-body">

                    <ul class="list-group list-group-flush">

                        <li v-for="(step,index) in recipe.steps" :key="step" class="list-group-item">{{index+1}}.{{step}}</li>
                    
                    </ul>
                </div>
            </div>
                
        </div>
       
</div>


<!-- loading -->
<div v-if="loading">
    <div class="conrainer">
        <div class="row">
            <div class="col">
                <SpinnerComponent></SpinnerComponent>
            </div>
        </div>
    </div>
</div>


<!-- error -->
<div v-if="!loading && errorMessage" >
    <div class="container">
        <div class="row">
            <div class="col">
                <p class="h3 text-danger fw-bold">{{errorMessage}}</p>
            </div>
        </div>
    </div>
</div>
</template>
<script>
    import SpinnerComponent from '@/components/SpinnerComponent.vue';

import { RecipeService} from "@/services/RecipeService"
export default {
    data: function () {
        return {
            recipeId: this.$route.params.recipeId,
            number:0,
            loading: false,
            recipe: [], 
            errorMessage: null
        };
    },
    created: async function () {
        try {
            this.loading = true;
            let response = await RecipeService.getRecipe(this.recipeId);
            this.recipe = response.data;
            this.loading = false;
        }
        catch (error) {
            this.errorMessage = error;
            this.loading = false;
        }
    },
    components: {SpinnerComponent}

    }
</script>
